body{
    margin: auto;
    padding: 0;
    font-family: sans-serif;
}

.navbar{
    overflow: hidden;
    background: none;
    color: #fff;
    height: 100px;
    z-index: 1000;
}

.container{
    width: 100%;
    margin: auto;
    margin-top: 0;
    padding: 0;
}

.logo{
    float: left;
    display: block;
    margin: auto;
    color: #fff;
    text-decoration: none;

}

a{
    text-decoration: none;
}

